.ps--active-x {
  /* overflow: hidden; */
  /* display: none; */
}

.ps__rail-y {
  display: none;
  opacity: 0;
  transition: background-color 0.2s linear, opacity 0.2s linear;
  width: 15px;
  right: 0;
  position: absolute;
}

/* .ps--active-x > .ps__rail-x, */
.ps--active-y > .ps__rail-y {
  display: block;
  background-color: transparent;
}

/* .ps:hover > .ps__rail-x, */
.ps:hover > .ps__rail-y,
/* .ps--focus > .ps__rail-x, */
.ps--focus > .ps__rail-y,
/* .ps--scrolling-x > .ps__rail-x, */
.ps--scrolling-y > .ps__rail-y {
  opacity: 0.6;
}

